import "./App.css";
import logo from "./logo.svg";
import { useEffect } from "react";

// 001
import jQuery from "jquery";
import "op-payment-widget-v3/build/op-payment-widget-v3.css";

// 002
window.$ = jQuery;
const checkoutList = require("op-payment-widget-v3");

function App() {
  useEffect(() => {
    // 003
    window.$('#paymentNetworks').empty();

    checkoutList("paymentNetworks", {
      payButton: "submitBtn",
      payButtonContainer: "submitBtnContainer",
      widgetCssUrl: window.location.origin + "/payment-page-react-v1/widget.min.css",
    });
  }, []);

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>Payment Page React v1</p>
      </header>

      <div className="App-body">
        {/* 004 */}
        <div id="paymentNetworks" className="payment-networks-container"></div>
        <div id="submitBtnContainer" className="submit-buttons-container">
          <button
            id="submitBtn"
            type="button"
            className="submit-button"
          ></button>
        </div>
      </div>
    </div>
  );
}

export default App;
